import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    curMenu:'',
    trResults:{ tras:[] },
    curTid:'',
  },
  getters: {
    getCounts:state => {
      if(state.trResults.tras!=undefined)
        return state.trResults.tras.length;
      else 
        return -1;
    },
    tras:state => {
      if(state.trResults.tras!=undefined)
        return state.trResults.tras;
      else
        return [];
    },
    // 현재 상세페이지에 열린 거래내역
    curTid:state=> {
      return state.curTid;
    },
    curTra:state => {
      return state.trResults.tras.find(tra=>tra.tid==state.curTid);
    }
  },
  mutations: {
    setCurMenu(state,payload) {
      state.curMenu = payload
    },
    setCurTid(state,payload) {
      state.curTid = payload; // tid
    },
    setTrResults(state,payload) {
      switch(state.curMenu) {
        case 'tras':
          console.log(payload)
          state.trResults = payload
          break
        case 'btras':
          console.log(payload)
          state.trResults.tras = payload
          state.trResults.result = 'ok'
          state.trResults.count = payload.length;
          break
        case 'store':
          console.log(payload)
          state.trResults = { tras:[] }
          break
      }
    },
  },
  actions: {
    loadTransactions({commit},payload) {
      // mutation 'setTraResults' commit
      commit('setTrResults',payload)
    },
  },
  modules: {
  }
})
