<template>
    <div class="page-sidebar shadow-base">
        <a href="" class="sb-brand"><img src="../assets/images/logo_brand.png" alt="InstaPay"></a>
        <div class="sidebar-menu-wrapper">
            <ul class="sidebar-menu">
                <li class="nav-item" id="menu_s1" @click="move('tras')"><a href="javascript:void(0);">거래내역</a></li>
                <li class="nav-item" id="menu_s2"><a href="javascript:void(0);">취소환불</a></li>
                <li class="nav-item" id="menu_s3" @click="move('store')"><a href="javascript:void(0);">가맹점 관리</a></li>
                <div class="divider"></div>
                <li class="nav-item" id="menu_s91" @click="move('btras')"><a href="javascript:void(0);">도서주문</a></li>
            </ul>
        </div>
    </div>
</template>

<script>
export default {
    name:"PageSidebar",
    data() {
        return {
            curMenu: ''
        }
    },
    methods: {
        move(pathname) {
            this.$router.push(pathname).catch(()=>{}); // 'Avoided redundant navigation' 에러 방지 
        },
        setCurMenu(curMenu) {
            //console.log(curMenu)
            this.$store.commit('setCurMenu', curMenu)
        }
    },
    mounted() {
        console.log('mounted', this.currentMenu)
        this.setCurMenu(this.currentMenu)
    },
    computed: {
        currentMenu:function() {
            return this.$route.path.slice(1)
        }
    },
    watch: {
        currentMenu(newVal) {
            console.log('watch', newVal)
            this.setCurMenu(this.currentMenu)
        }
    }
}
</script>
<style scoped>
.page-sidebar { background-color:var(--bg-white);color:var(--text-grey); }
/* .sb-brand { font-size:20px;font-weight:bold; text-align:center; } */
</style>